/* Add this CSS to your stylesheet */
nav ul{
    display: flex;
}

nav li{
    list-style-type: none;
}

.nav-link {
    padding: 8px 12px;
    margin-right: 10px;
    background-color: #007bff;
    color: #fff;
    text-decoration: none;
    border: none;
    border-radius: 5px;
  }
  
  .nav-link:hover {
    background-color: #0056b3;
  }

  #main {
    display: flex;
} 
#productList {
    display: block;
    height: 600px;
    width: 15%;
    overflow: scroll;
}
  .icon {
    width: 30px;
}

.image {
    width: 300px;
}